}

body {
  background: url(/mint/static/media/bg.34b65f05.png);
  background-size: cover;
  background-repeat: no-repeat;
}

.header {
  position: absolute;
  top: 0px;
  right: 5%;
  padding: 20px;
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* background-color: #040a16; */
}

.flex {
  display: flex;
}

.w-50 {
  width: 50%;
  border: 3px solid red;
}

.main {
  /* background: url(./images/background.png) center center / cover; */
  height: auto;
  width: 100%;
  padding-top: 150px;
  padding-bottom: 30px;
  display: flex;
}

@media (min-width: 992px) {
  .main {
    height: 100vh;
    padding: 0px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    /* flex-direction: row-reverse; */
    -webkit-box-align: center;
    padding-top: 150px;
  }

  .main .inner {
    width: 45%;
    margin: 0px auto;
  }
}

.connectHeader {
  font-family: "Upheaval TT (BRK)", sans-serif;
  color: #54545c;
  font-size: 60px;
  font-weight: 400;
  letter-spacing: 8px;
  line-height: 1.6;
}

.slick-arrow {
  display: none !important;
}

.icons {
  display: flex;
  grid-gap: 1.6em;
  gap: 1.6em;
}

.slideImage {
  max-width: 90vw;
  margin: 0 auto;
}

.slideImage img {
  margin: 0 auto;
  border-radius: 5px;
  border: 2px solid #fff;
}

.logo p {
  text-align: left;
  /* min-width: 40vw; */
  color: #fefefe;
  text-transform: uppercase;
  letter-spacing: 5px;
  line-height: 4rem;
  font-size: 2.3rem;
  background: #040a16;
}

.logo img {
  max-height: 60px;
}

@media (max-width: 767px) {
  .logo img {
    max-height: 33px;
  }
}

iframe {
  display: none;
}

@media (max-width: 767px) {
  .main {
    flex-direction: column-reverse;
  }

  .main .inner {
    padding-bottom: 40px;
  }

  .header {
    background: #040a16;
  }

  .logo p {
    background: transparent;
  }

  .main {
    padding-top: 200px;
  }

  .hide-sm {
    display: none;
  }
}

.info {
  padding: 10px 0;
  margin: 0 10px;
  border-radius: 20px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  line-height: 2em;
}

@media (min-width: 768px) {
  .show-sm {
    display: none;
  }
}

